.Stopwatch {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Stopwatch-display {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-order: 1;
      order: 1;
  -ms-flex-item-align: auto;
      align-self: auto;
  width: 100%;
  height: 100%;
}

.Stopwatch-number {
  color: #ffffff;
}

.Stopwatch-button-group {
  margin: 0 auto;
  padding: 0 5px;
}

.Stopwatch-button {
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}

@media (max-width: 575px) {
  .Stopwatch-number {
    font-size: 2em;
  }
}

@media (min-width: 576px) {
  .Stopwatch-number {
    font-size: 3em;
  }
}

@media (min-width: 768px) {
  .Stopwatch-number {
    font-size: 6em;
  }
}

@media (min-width: 992px) {
  .Stopwatch-number {
    font-size: 9em;
  }
}

@media (min-width: 1200px) {
  .Stopwatch-number {
    font-size: 12em;
  }
}



